.descriptionBox{
    margin:120px 170px;
}

.decriptionBoxNavigator{
    display: flex;
}

.decriptionBoxNavBox{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}

.descriptionBoxFade{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #FBFBFB;
    color: #555;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}

.descriptionBoxDescription{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #D0D0D0;
    padding: 48px;
    padding-bottom: 70px;
}

@media(max-width:1280px){
    .descriptionBox{
        margin: 60px;
    }
}
@media(max-width:800px){
    .descriptionBox{
        margin: 60px 20px;
    }
    .descriptionBoxDescription{
        font-size: 14px;
    }
}