.item{
    width: 280px;
}

.item img{
    width: 280px;
}

.item p{
    margin: 6px 0px;
}

.itemPrices{
    display: flex;
    gap: 20px;
}

.itemPricesNew{
    color:#374151;
    font-size: 18px;
    font-weight: 600;

}

.itemPricesOld{
    color:#8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}

.item img{
    height: 250px;
    width: 200px;
}

@media(max-width:1280px){
    .item{
        width: 220px;
        font-size: 14px;
    }
    .item img{
        width: 220px;
    }
    .itemPricesOld{
        font-size: 14px;
    }
}
@media(max-with:1024px){
    .item{
        width: 170px;
        font-size: 13px;
    }
    .item img{
        width: 170px;
    }
    .itemPricesOld{
        font-size: 13px;
    }
}
@media(max-with:800px){
    .item{
        width: 120px;
        font-size: 12px;
    }
    .item img{
        width: 120px;
    }
    .itemPricesOld{
        font-size: 12px;
    }
}
@media(max-with:500px){
    .item{
        width: 160px;
    }
    .item img{
        width: 160px;
    }
}