.iconImage{
  height: 600px;
  width: 600px;
}

.arrowImage{
    width: 50px;
    height: auto;
}

.handImage{
    width: 80px;
}

.home{
    height: 100vh;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
    display: flex;
}
.homeLeft{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}

.homeLeft h2{
    color: #090909;
    font-size: 26px;
    font-weight: 700;
}

.homeLeft p{
    color: #171717;
    font-size: 100px;
    font-weight: 700;
}

.handIcon{
    display: flex;
    align-items: center;
    gap: 20px;
}

.homeLatest{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.homeRight{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media(max-width:1280px){
    .homeLeft{
        padding-left: 100px;
    }
    .homeLeft h2{
        font-size: 22px;
    }
    .homeLeft p{
        font-size: 60px;
    }
    .handIcon img{
        width: 80px;
    }
    .homeLatest{
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .homeRight img{
        width: 500px;
    }
}
@media(max-width:1024px){
    .homeLeft{
        padding-left: 80px;
    }
    .homeLeft h2{
        font-size: 20px;
    }
    .homeLeft p{
        font-size: 50px;
    }
    .handIcon img{
        width: 65px;
    }
    .homeLatest{
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .homeRight img{
        width: 400px;
    }
}
@media(max-width:800px){
    .homeLeft{
        padding-left: 30px;
    }
    .homeLeft h2{
        font-size: 16px;
    }
    .homeLeft p{
        font-size: 40px;
    }
    .handIcon img{
        width: 50px;
    }
    .homeLatest{
        width: 175px;
        height: 40px;
        font-size: 13px;
    }
    .homeRight img{
        width: 300px;
    }
}
@media(max-width:500px){
    .home{
        flex-direction: column;
    }
    .homeRight{
        display: none;
    }
    .homeLeft h2{
        font-size: 18px;
    }
    .homeLeft p{
        font-size: 50px;
    }
    .handIcon img{
        width: 55px;
    }
    .homeLatest{
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
}