.NewCollections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
}
.NewCollections h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.NewCollections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 20px;
}

@media(max-width:1280px){
    .NewCollections h1{
        font-size: 40px;
    }
    .NewCollections hr{
        width: 160px;
        height: 4px;
    }
    .collections{
        gap: 20px;
        margin-top: 30px;
    }
}
@media(max-width:1024px){
    .NewCollections h1{
        font-size: 30px;
    }
    .NewCollections hr{
        width: 120px;
        height: 3px;
    }
    .collections{
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width:800px){
    .NewCollections h1{
        font-size: 20px;
    }
    .NewCollections hr{
        width: 100px;
    }
    .collections{
        gap:5px;
        grid-template-columns: 1fr 1fr;
    }
    .collections img {
        max-width: 100%; /* Set max-width for images to 100% */
        height: auto; /* Maintain aspect ratio */
    }
}
@media(max-width:500px){
    .collections{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}